.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.25);
}

.modalDialog {
  width: 100%;
  max-width: 550px;
  background: white;
  position: relative;
  margin: 0 20px;
  max-height: calc(100vh - 40px);
  text-align: left;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 #00000033, 0 6px 20px 0 #00000030;
}

.modalHeader,
.modalFooter {
  display: flex;
  align-items: center;
  padding: 1rem;
}

.modalHeader {
  border-bottom: 1px solid #dbdbdb;
  justify-content: space-between;
}

.modalFooter {
  border-top: 1px solid #dbdbdb;
  justify-content: flex-end;
}

.modalClose {
  cursor: pointer;
  padding: 1rem;
  margin: -1rem -1rem -1rem auto;
}

.modalBody {
  overflow: auto;
}

.modalContent {
  padding: 1rem;
}

.modalTitle {
  margin: 0;
}

.modalHeader {
  font: 400 14px robotoSlab;
  color: #1c182b;
}

.modalContent > p {
  font: 400 14px robotoSlab;
  color: #1c182b;
  margin-bottom: 4px;
}

.modalFooter > button {
  font: 400 14px robotoSlab;
  color: #1c182b;
  margin-left: 10px;
  padding: 5px 10px;
  cursor: pointer;
}